<template>
  <v-container
    id="regular-tables-view"
    fluid
    tag="section"
  >
    <material-card
      icon="mdi-clipboard-text"
      icon-small
      color="primary"
      title="Equipo"
    >
    <v-card>
                <v-card-title>
                  <span class="text-h4"><b>{{ formTitulo }}</b></span>
                </v-card-title>
                <v-card-text>
                  <v-form ref="form" v-model="valid">
                    {{equipo}}
                    <v-container>
                      <v-row>
                        <v-col
                          cols="12"
                          sm="3"
                          md="3"
                        >
                          <v-text-field
                            v-model="equipo.codigoActivo"
                            label="Codigo Activo"
                            hint="Ej. OJ-14-003645"
                            :counter="15"
                            :rules="fieldRules"
                            :autofocus="true"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="9"
                          md="9"
                        >
                          <v-text-field
                            v-model="equipo.descripcion"
                            label="Descripción"
                            hint="Ejemplo: Monitor, marca DELL, modelo P24V"
                            :counter="200"
                            :rules="fieldRules"
                            
                            persistent-hint
                          />
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-autocomplete
                            v-model="equipo.categoriaId"
                            :items="lista_categoria"
                            item-text="descripcion"
                            item-value="id"
                            label="Categoria"
                            @change="(valor) => addCaracteristica(valor)"
                            hint="Seleccione la categoria"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                          <!-- {{equipo}} -->
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-autocomplete
                            v-model="equipo.marcaId"
                            :items="lista_marca"
                            item-text="descripcion"
                            item-value="id"
                            label="Marca"
                            @change="(valor) => descripcionMarca(valor)"
                            hint="Seleccione la marca"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                          <!-- {{equipo}} -->
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-autocomplete
                            v-model="equipo.modeloId"
                            :items="lista_modelo"
                            item-text="descripcion"
                            item-value="id"
                            label="Modelo"
                            @change="(valor) => descripcionModelo(valor)"
                            hint="Seleccione el modelo"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                          <!-- {{equipo}} -->
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-autocomplete
                            v-model="equipo.estadoEquipo"
                            :items="lista_estado"
                            item-text="descripcion"
                            item-value="descripcion"
                            label="Estado del Equipo"
                            hint="Seleccione el estado"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                          <!-- {{equipo}} -->
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-autocomplete
                            v-model="equipo.oficinaId"
                            :items="lista_oficina"
                            item-text="descripcion"
                            item-value="id"
                            label="Oficina"
                            hint="Seleccione la oficina"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                          <!-- {{equipo}} -->
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-menu
                            ref="menu"
                            v-model="menu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                            min-width="auto"
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-text-field
                                v-model="equipo.fechaAlta"
                                label="Seleccione la fecha de alta"
                                prepend-icon="mdi-calendar"
                                readonly
                                v-bind="attrs"
                                v-on="on"
                              ></v-text-field>
                            </template>
                            <v-date-picker
                              v-model="equipo.fechaAlta"
                              @input="menu = false"
                            >
                            </v-date-picker>
                          </v-menu>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="12"
                          md="12"
                        >
                          <v-text-field
                            v-model="equipo.observaciones"
                            label="Observaciones"
                            :counter="200"
                            persistent-hint
                          />
                        </v-col>
                      </v-row>
                      
                      <label><b><h3>CARACTERISTICAS: </h3></b></label>
                      <v-row>
                        <v-col
                        v-for="(caracteristica, index) in caracteristicas" :key="index"
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <div v-for="(descripcion, index) in descripcionCaracteristicas" :key="index"></div>
                          <v-text-field
                            v-model="caracteristica.valor"
                            :label="descripcionCaracteristicas[index]"
                            :counter="20"
                          />
                        </v-col>
                      </v-row>

                      <!-- {{caracteristicas}} -->

                    </v-container>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="close"
                    :to="'/tables/equipo'"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    :loading="estadoBoton"
                    @click="guardar"
                    :disabled="!valid"
                    color="primary"
                  >
                    Guardar
                  </v-btn>
                </v-card-actions>
              </v-card>

    </material-card>
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color=""
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>


<script>
import * as catService from "../../services/categoriaService"
import * as equService from "../../services/equipoService"
import * as marService from "../../services/distritoService"
import * as modService from "../../services/modeloService"
import * as carService from "../../services/caracteristicaService"


  export default {
    data () {
      return {
        categoria: null,
        descripcionEquipo: [],
        descripcionCaracteristicas: [],
        caracteristicas: [],
        estadoBoton: false,
        dialog: false,
        dialogDelete: false,
        lista_equipo: [],
        lista_marca: [],
        lista_caracteristica: [],
        lista_categoria: [],
        lista_modelo: [],
        lista_estado: [
          'Bueno', 'Regular', 'Malo'
        ],
        lista_oficina: [
          1, 2, 3
        ],
        cargando: true,
        equipo: {
          // caracteristicas: [{id:null, valor: null}]
          descripcion: '',
          // idMarcas: []
        },
        search: '',
        editedIndex: -1,
        defaultEquipo: {
          descripcion: '',
          // idMarcas: []
        },
        color: '',
        snackbar: false,
        text: '',
        timeout: 3000,
        valid: true,
        fieldRules: [
          v => !!v || 'El campo es requerido',
          v => (v && v.length <= 50) || 'El campo debe ser menor a 50 caracteres',
        ],
        menu: false,
      }
    },
    computed: {
      formTitulo () {
        return this.editedIndex === -1 ? 'Nuevo Registro' : 'Editar Registro'
      },
      
      
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeEliminar()
      },
      
    },
    async mounted () {
      this.obtener()
      this.obtenerCategoria()
    },
    methods: {

      getItemText(item) {
        return `${item.first_value} ${item.second_value}`;
      },

      async addCaracteristica (valor) {
        this.caracteristicas = []
        this.lista_caracteristica = []
        this.descripcionCaracteristicas = []
        await this.obtenerCaracteristicas(valor)
        // console.log(this.lista_caracteristica);
        
        for (let i = 0; i < this.lista_caracteristica.length; i++) {
          this.caracteristicas.push({ caracteristicaId: this.lista_caracteristica[i].id, valor: ''  });
          this.descripcionCaracteristicas.push(this.lista_caracteristica[i].descripcion)
        }
        // console.log(valor);
        let categoriaAux = await catService.listarId(valor)
        this.descripcionEquipo = []
        this.descripcionEquipo[0] = categoriaAux.data.descripcion
        this.equipo.descripcion = `${this.descripcionEquipo[0]}`
        await this.obtenerMarca(valor)
        
        // console.log(this.descripcionEquipo);
      },
      async descripcionMarca (valor) {
        let marcaAux = await marService.listarId(valor)
        this.descripcionEquipo[1] = marcaAux.data.descripcion
        this.equipo.descripcion = `${this.descripcionEquipo[0]}, marca ${this.descripcionEquipo[1]}`
        await this.obtenerModelo (valor)
        // console.log(this.descripcionEquipo);
      },
      async descripcionModelo (valor) {
        let modeloAux = await modService.listarId(valor)
        this.descripcionEquipo[2] = modeloAux.data.descripcion
        this.equipo.descripcion = `${this.descripcionEquipo[0]}, marca ${this.descripcionEquipo[1]}, modelo ${this.descripcionEquipo[2]}`
        console.log(this.equipo.descripcion);
      },
      async obtener () {
        const res = await equService.listar()
        // console.log(res)
        this.lista_equipo = res.data
        this.cargando = false
      },
      // async obtenerMarca (id) {
      //   const res = await marService.listarByCategoria(id)
      //   // console.log(res)
      //   this.lista_marca = res.data
      //   this.cargando = false
      // },
      async obtenerModelo (id) {
        const res = await modService.listarByMarca(id)
        // console.log(res)
        this.lista_modelo = res.data
        this.cargando = false
      },
      // async obtenerCaracteristicas (id) {
      //   const res = await carService.listarByCategoria(id)
      //   console.log(res.data)
      //   this.lista_caracteristica = res.data
      //   this.cargando = false
      // },
      async obtenerCategoria () {
        const res = await catService.listar()
        // console.log(res)
        this.lista_categoria = res.data
        this.cargando = false
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.equipo = Object.assign({}, this.defaultEquipo)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      async guardar () {
        this.equipo = Object.assign(this.equipo, {caracteristicas: this.caracteristicas});
        console.log(this.equipo);
        let res = null
        this.validate();
        this.estadoBoton = true
        if (this.editedIndex > -1) {
          const mod = this.lista_equipo[this.editedIndex]
          res = await equService.modificar(this.equipo, mod.id)
          Object.assign(this.lista_equipo[this.editedIndex], this.equipo)
        } else {
          res = await equService.guardar(this.equipo)
          this.lista_equipo.push(this.equipo)
          this.$router.push({ name: "equipo/Equipo" })
          // window.location.href = '/tables/equipo'
        }
        if(res.status == 200){
          this.text = "Registro guardado"
          this.snackbar = true
          this.color = "green"
        } else {
          this.text = res.data.message
          this.snackbar = true
          this.color = "dark-red"
        }
        this.obtener()
        this.estadoBoton = false
        this.close()
      },
      editar (item) {
        this.editedIndex = this.lista_equipo.indexOf(item)
        this.equipo = Object.assign({}, item)
        this.dialog = true
      },
      validate() {
        this.$refs.form.validate()
      }
    },
  }
</script>
