import axios from "axios"
import { data } from "browserslist";
import { getHeader } from "../config/config";

const url = process.env.VUE_APP_API_URL
const entidad = 'distrito'

export const listar = async function(){
  // let {data} = await axios.get(`${url}/marca`, {headers: getHeader()});
  let data = await axios.get(`${url}/${entidad}`, {headers: getHeader()});
  return data;
}

export const listarId = async function(id){
  // let {data} = await axios.get(`${url}/marca`, {headers: getHeader()});
  let data = await axios.get(`${url}/${entidad}/${id}`, {headers: getHeader()});
  return data;
}

export const guardar = async function(datos){
  // let {data} = await axios.post(`${url}/marca/crear`, datos, {headers: getHeader()});
  let data = await axios.post(`${url}/${entidad}/create`, datos, {headers: getHeader()});
  return data;
}

export const modificar = async function(datos, id){
  // let {data} = await axios.put(`${url}/${entidad}/${id}`, datos, {headers: getHeader()});
  let data = await axios.put(`${url}/${entidad}/update/${id}`, datos, {headers: getHeader()});
  return data;
}

export const eliminar = async function(id){
  // let {data} = await axios.delete(`${url}/${entidad}/delete/${id}`, {headers: getHeader()});
  let data = await axios.put(`${url}/${entidad}/delete/${id}`, {headers: getHeader()});
  return data;
}